import axios from 'axios'
import back from '@/components/back'
export default {
	data() {
		return {
			DrId:null,
			DrName:"",
			drInfo:null,
			diseList:null,
			isAgree:true
		};
	},
	components:{
		back
	},
	created() {
		this.DrId = this.$store.state.userInfo.Data.User.DoctorId
		this.DrName = this.$store.state.userInfo.Data.User.DoctorName
		this.getMsg()
	},
	mounted() {
		
	},
	methods: {
		getMsg(){
			if(this.$store.state.SaveDiease){
				this.diseList = this.$store.state.SaveDiease
				this.$get('PlatFormAPI/Doctor/QueryDoctorInfoByDrId', {//获取医生信息
					DrId:this.DrId
				}).then(o => {
					this.drInfo = o
				})
			}else{
				axios.all([
					this.$get('PlatFormAPI/Doctor/QueryDoctorInfoByDrId', {//获取医生信息
						DrId:this.DrId
					}),
					this.$get('PlatFormAPI/DoctorReferral/QueryDoctorDisGroup', {//获取医生的疾病分组
						DrId:this.DrId,
						DisGroupTypeId:0
					})
				]).then(o => {
					this.drInfo = o[0]
					this.diseList = o[1] 
					this.$store.dispatch('pushSaveDiease',this.diseList)
				})
			}
		},
		cancelDise(i,k){
			this.diseList.splice(k,1)
			this.$store.dispatch('pushSaveDiease',this.diseList)
		},
		sumbitMsg(){
			if(!this.isAgree){
				this.$toast('请先勾选并同意分级合作协议')
				return 
			}
			//确认提交
			axios.all([
				this.$post('PlatFormAPI/DoctorReferral/OpenDrReferral', {//开通医生的分级诊疗
					DrId:this.DrId
				})
			]).then(o => {
				//跳转到分级诊疗的设置页面
				this.$bridge.callhandler('gotofenjizhuanzhen', true)
			})
		}
	}
};



